<template>
  <div class="search">
    <van-search
      v-model="value"
      show-action
      shape="round"
      placeholder="请输入搜索关键词"
      @input="onSearch"
    >
      <template #action>
        <div @click="cancel">取消</div>
      </template>
    </van-search>
    <div class="detail" v-if="value == ''">
      <div class="title">热搜榜</div>
      <div class="detail-list" v-for="(item, index) in detaillist" :key="index">
        {{ index + 1 }}、&ensp;&ensp;&ensp; {{ item.searchWord }}
      </div>
    </div>
    <div class="search-list" v-if="value != ''">
      <van-cell
        v-for="(item, index) in songs"
        :key="index"
        :title="item.name"
        border
        icon="search"
        @click="add(item.id)"
      />
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant'
export default {
  data () {
    return {
      value: '',
      songs: [],
      detaillist: []
    }
  },
  created () {
    this.searchhot()

  },
  mounted () {
    this.detail()
  },
  methods: {
    // 搜索
    onSearch () {
      let data = {
        keywords: this.value
      }
      this.api.search(data).then(res => {
        if (res.data.code == 200) {
          this.songs = res.data.result.songs
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        Toast(err.data.msg)
      })
    },
    // 取消
    cancel () {
      this.$router.push('/')
    },
    // 热搜列表(详细)
    searchhot () {
      this.api.searchhot().then(res => {
        if (res.data.code == 200) {
          this.detaillist = res.data.data
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        Toast(err.data.msg)
      })
    },
    add (id) {
      let data = {
        id: id
      }
      // https://music.163.com/song/media/outer/url?id=1426112587.mp3
      this.api.songurl(data).then(res => {
        if (res.data.code == 200) {
          Toast(res.data.data[0].id)
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        Toast(err.data.msg)
      })
    },
  }
}

</script>

<style lang="less" scoped>
.search {
  margin: 0 24px 60px 24px;

  /deep/ .van-cell {
    line-height: 50px;
  }

  /deep/ .van-field__control {
    font-size: 24px;
  }

  /deep/ .van-search__action {
    font-size: 24px;
  }

  /deep/ .van-icon {
    font-size: 28px;
  }

  .detail {
    background: #fff;
    box-shadow: 4px 4px 8px 4px rgba(0, 0, 0, 0.05);
    border-radius: 15px;
    padding: 20px;
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .title {
      width: 100%;
      font-size: 28px;
      color: rgba(0, 0, 0, 7);
      text-align: left;
      margin-bottom: 10px;
    }

    .detail-list {
      width: 50%;
      font-size: 24px;
      text-align: left;
      padding: 10px 0px;
    }
  }

  .search-list {
    /deep/ .van-cell__title {
      font-size: 24px;
      text-align: left;
    }
    /deep/ .van-icon {
      font-size: 28px !important;
    }
    /deep/ .van-cell__left-icon,
    .van-cell__right-icon {
      line-height: 50px;
      margin-right: 20px;
    }
  }
}
</style>